/*eslint-disable*/
import React from "react";
import { Container, Button } from "reactstrap";
import { Slide, Roll } from "react-reveal";

// core components

function IndexHeader() {
  let pageHeader = React.createRef();

  React.useEffect(() => {
    if (window.innerWidth > 991) {
      const updateScroll = () => {
        let windowScrollTop = window.pageYOffset / 3;
        pageHeader.current.style.transform =
          "translate3d(0," + windowScrollTop + "px,0)";
      };
      window.addEventListener("scroll", updateScroll);
      return function cleanup() {
        window.removeEventListener("scroll", updateScroll);
      };
    }
  });

  return (
    <>
      <div className="page-header red-filter" filter-color="blue">
        <div
          className="page-header-image"
          style={{
            backgroundImage: "url(" + require("assets/img/header.jpg") + ")"
          }}
          ref={pageHeader}
        ></div>
        <Container>
          <div className="content-center brand">
            <Roll right delay="1500">
              <img
                alt="..."
                className="n-logo"
                src={require("assets/img/parot.png")}
              ></img>
            </Roll>
            <Slide left cascade delay="1750">
              <div>
                <h1>X-Compact</h1>
                <h2>FXD-4200</h2>
                <h3 className="h1-seo">Full 4K Drone</h3>
              </div>
            </Slide>
          </div>
        </Container>
      </div>
    </>
  );
}

export default IndexHeader;
